$(document).ready(function() {

    var loader = $('#preloader');
    setTimeout(function () {
        if (!loader.hasClass('ok')) {
            loader.addClass('ok');
        }
    }, 1500);


    var body =$('body'),
        mobileMenu = $('.mobile-group'),
        menuButton = $('.btn-mobile'),
        hideMenuClick = $('#menu-mobile a'),
        firstBtn = $('.first-step'),
        firstContent = $('.first-step-content'),
        secondBtn = $('.second-step'),
        secondContent = $('.second-step-content'),
        thirdBtn = $('.third-step'),
        thirdContent = $('.third-step-content'),
        btnClose = $('.btn-close');


    menuButton.on('click', function () {
        body.toggleClass('open_menu');
        mobileMenu.toggleClass('show');
        menuButton.toggleClass('close');

        if(mobileMenu.hasClass('show')) {
            hideMenuClick.on('click', function () {
                body.removeClass('open_menu');
                mobileMenu.removeClass('show');
                menuButton.removeClass('close');
            });
        }
    });


    firstBtn.on('click', function () {
        firstContent.addClass('show');
        body.addClass('remove-scroll');
    });
    secondBtn.on('click', function () {
        secondContent.addClass('show');
        body.addClass('remove-scroll');
    });
    thirdBtn.on('click', function () {
        thirdContent.addClass('show');
        body.addClass('remove-scroll');
    });


    btnClose.on('click', function () {
        $(this).parents('.modal-custom').removeClass('show');
        body.removeClass('remove-scroll');
        $.fn.fullpage.moveTo('product');
    });


    $('#fullpage').fullpage({

        //Navigation
        menu: '#menu, #menu-mobile',
        lockAnchors: false,
        anchors:['mission', 'about', 'product', 'cases', 'reviews', 'news', 'contact'],
        navigation: false,
        navigationPosition: 'right',
        navigationTooltips: ['mission', 'about', 'product', 'cases', 'reviews', 'news', 'contact'],
        showActiveTooltip: false,
        slidesNavigation: false,
        slidesNavPosition: 'bottom',

        //Scrolling
        css3: true,
        scrollingSpeed: 700,
        autoScrolling: true,
        fitToSection: false,
        fitToSectionDelay: 100,
        scrollBar: false,
        easing: 'easeInOutCubic',
        easingcss3: 'ease',
        loopBottom: false,
        loopTop: false,
        loopHorizontal: true,
        continuousVertical: false,
        continuousHorizontal: false,
        scrollHorizontally: false,
        interlockedSlides: false,
        dragAndMove: true,
        offsetSections: false,
        resetSliders: true,
        fadingEffect: false,
        normalScrollElements: '#element1, .element2',
        scrollOverflow: false,
        scrollOverflowReset: false,
        scrollOverflowOptions: null,
        touchSensitivity: 15,
        normalScrollElementTouchThreshold: 5,
        bigSectionsDestination: null,

        //Accessibility
        keyboardScrolling: true,
        animateAnchor: true,
        recordHistory: true,

        //Design
        controlArrows: false,
        verticalCentered: true,
        paddingTop: '3em',
        paddingBottom: '3rem',
        fixedElements: '#header, #footer',
        responsiveWidth: 1200,
        responsiveHeight: 800,
        responsiveSlides: true,
        parallax: false,
        parallaxOptions: {type: 'reveal', percentage: 62, property: 'translate'},

        //Custom selectors
        sectionSelector: '.section',
        slideSelector: '.slide',

        lazyLoading: true,

        //events
        onLeave: function(index, nextIndex, direction){
            var numbers = $('.number-page'),
                scrollDown = $('.scroll');

            if(nextIndex  === 1) {
                numbers.html('<div class="number-page animated fadeInUp">01</div>');
                scrollDown.attr('href','#about').removeClass('go_home');
                scrollDown.children('span').html('scroll');
            } else if (nextIndex  === 2) {
                numbers.html('<div class="number-page animated fadeInUp">02</div>');
                scrollDown.attr('href','#product').removeClass('go_home');
                scrollDown.children('span').html('scroll');
            } else if (nextIndex  === 3) {
                numbers.html('<div class="number-page animated fadeInUp">03</div>');
                scrollDown.attr('href','#cases').removeClass('go_home');
                scrollDown.children('span').html('scroll');
            } else if (nextIndex  === 4) {
                numbers.html('<div class="number-page animated fadeInUp">04</div>');
                scrollDown.attr('href','#reviews').removeClass('go_home');
                scrollDown.children('span').html('scroll');
            } else if (nextIndex  === 5) {
                numbers.html('<div class="number-page animated fadeInUp">05</div>');
                scrollDown.attr('href','#news').removeClass('go_home');
                scrollDown.children('span').html('scroll');
            } else if (nextIndex  === 6) {
                numbers.html('<div class="number-page animated fadeInUp">06</div>');
                scrollDown.attr('href','#contact').removeClass('go_home');
                scrollDown.children('span').html('scroll');
            } else if (nextIndex  === 7)  {
                numbers.html('<div class="number-page animated fadeInUp">07</div>');
                scrollDown.attr('href','#mission').addClass('go_home');
                scrollDown.children('span').html('scroll top');
            }
        },
        afterLoad: function(anchorLink, index){},
        afterRender: function(){},
        afterResize: function(){},
        afterResponsive: function(isResponsive){},
        afterSlideLoad: function(anchorLink, index, slideAnchor, slideIndex){},
        onSlideLeave: function(anchorLink, index, slideIndex, direction, nextSlideIndex){}
    });

});

$(document).on('scroll', function () {
    scrollMenu();
});

function scrollMenu() {
    if($('#header').hasClass('scroll') && $('#header').offset().top === 32) {
        $('#header').removeClass('scroll')
    } else  if ($('#header').offset().top > 32) {
        $('#header').addClass('scroll')
    }
}